#adminmenu #toplevel_page_op-suite {
    .wp-menu-image {
        position: relative;
        top: 6px;
        left: 8px;
        background: url("../img/logo-white.svg") no-repeat top left !important;
        background-size: 22px 22px !important;

        img {
            display: none;
        }
    }

    &:hover {
        .wp-menu-image {
            background-image: url("../img/logo-blue.svg") !important;
        }
    }

    .wp-menu-image::before {
        content: "";
        display: none;
    }
}



